body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  
}

h1 {
  font-size: 40px;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
  color: red; 
}

h2 {
  font-size: 35px;
  font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
  color: red;
}

.welcome-page {
  text-align: center;
  /* background: #ccc; */
  min-height: 75vh;

  
}

img {
  position: relative;
  -webkit-animation-name: move;
          animation-name: move;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

@-webkit-keyframes move {
  0% {right: -40vw;}
  100% {right: 20vw; }
}

@keyframes move {
  0% {right: -40vw;}
  100% {right: 20vw; }
}

.session-container {
  display: grid;
  grid-template-columns: 1fr 2fr;
  grid-gap: 20px;
}

.session-form {
  border-right: 1px solid lightblue;
}

input {
  padding: 10px;
}

.form{
  display: grid;
  grid-gap: 20px;
  /* background: lightcoral; */
  text-align: center;
  
  justify-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-line-pack: center;
      align-content: center;

}

.form div {
  display: grid;
  grid-template-columns: 1fr 1fr;
  font-family: cursive;
  font-size: 20px;
  
}

.form-submit {
  /* align-content: center;
  align-items: center;
  justify-content: center;
  justify-items: center;
  display: flex; */
}

button {
  background-color: #333;
  color: #fff;
  padding: 0.6rem 1.3rem;
  text-decoration: none;
  border: 0;
  font-family: cursive;
  font-size: 16px;
  /* align-self: center;
  justify-self: center; */
}

.form-footer aside{
  font-family: 'Times New Roman', Times, serif;
  font-size: 23px;
}





a {
  text-decoration: none;
  /* align-self: center; */
  font-family: 'Times New Roman', Times, serif;
  font-size: 23px;
  color: red;

  
}



